<script>
import api from '../utils/api'
import footerBar from '@/components/footerBar.vue'
import topBar from '@/components/topBar.vue'
import qs from 'qs'

export default {
  data() {
    return {
      successPopup: 'hidden',
      categoryList: {1: 'sd'},
      monthProfitList: {},
      formInfo: {
        shop_name: '',
        shop_url: '',
        type: '2',
        main_product_type: '',
        monthly_profit: '',
        password: '',
        re_password: '',
        id: '',
        bd_id: ''
      },
      error: '',
      bd_qrcodes: []
    };
  },

  components: {
    footerBar,
    topBar
  },

  mounted() {
    let {id, bd_id} = this.$route.query
    this.formInfo.id = id || 0
    this.formInfo.bd_id = bd_id || window.localStorage.getItem('bd_id') || ''
    let st = setTimeout(() => {
      this.formInfo.shop_name = ''
      this.formInfo.password = ''
      clearTimeout(st)
    }, 2000);
    api.cdkCategoryList().then(res => res.data).then(data => {
      console.log('cdkCategoryList: ', data)
      if (data.code === 200) {
        this.categoryList = data.categories
      }
    })
    api.cdkMonthProfit().then(res => res.data).then(data => {
      if (data.code === 200) {
        this.monthProfitList = data.profits
      }
    })
    document.title = '超多客注册'
  },

  methods: {
    onSubmit() {
      // this.successPopup = 'visible'
      console.log(this.formInfo, 'formInfo')
      api.cdkPostExtraData(this.formInfo).then(res => res.data).then(data => {
        if (data.code === 200) {
          this.bd_qrcodes = data.bd_qrcodes
          this.successPopup = 'visible'
        } else {
          this.error = data.message
        }
      })
    },
    closePop () {
      this.successPopup = 'hidden'
      location.href = '/'
    }
  }
};
</script>

